
.c-navbar {
  position: absolute;
  z-index: 11;
  width: 100%;
}
.c-navbar-toggle {
  position: absolute;
  z-index: 102
}
[dir] .c-navbar-toggle {
  text-align: center;
  background: transparent;
  border: none;
  border-radius: 0;
}
[dir=ltr] .c-navbar-toggle {
  right: 0;
}
[dir=rtl] .c-navbar-toggle {
  left: 0;
}
[data-dpr="1"] .c-navbar-toggle {
  width: 44px;
  font-size: 16px;
  line-height: 44px;
}
[data-dpr="2"] .c-navbar-toggle {
  width: 88px;
  font-size: 32px;
  line-height: 88px;
}
[data-dpr="3"] .c-navbar-toggle {
  width: 132px;
  font-size: 48px;
  line-height: 132px;
}
[dir] .c-navbar-toggle:active {
  background-color: transparent;
}
.c-navbar-toggle.active {
  color: #38ADFF;
}
.c-navbar-menu {
  position: fixed;
  z-index: 101;
  top: 0;
  height: 100%;
  -webkit-transition: -webkit-transform 0.3s ease-in-out;
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out
}
[dir] .c-navbar-menu {
  background-color: white;
  box-shadow: 0 1px 3px rgb(204, 204, 204);
}
[dir=ltr] .c-navbar-menu {
  right: 0;
}
[dir=rtl] .c-navbar-menu {
  left: 0;
}
[data-dpr="1"] .c-navbar-menu {
  width: 44px;
}
[dir][data-dpr="1"] .c-navbar-menu {
  padding-top: 44px;
}
[dir=ltr][data-dpr="1"] .c-navbar-menu {
  -webkit-transform: translate3d(44px, 0, 0);
          transform: translate3d(44px, 0, 0);
}
[dir=rtl][data-dpr="1"] .c-navbar-menu {
  -webkit-transform: translate3d(-44px, 0, 0);
          transform: translate3d(-44px, 0, 0);
}
[data-dpr="2"] .c-navbar-menu {
  width: 88px;
}
[dir][data-dpr="2"] .c-navbar-menu {
  padding-top: 88px;
}
[dir=ltr][data-dpr="2"] .c-navbar-menu {
  -webkit-transform: translate3d(88px, 0, 0);
          transform: translate3d(88px, 0, 0);
}
[dir=rtl][data-dpr="2"] .c-navbar-menu {
  -webkit-transform: translate3d(-88px, 0, 0);
          transform: translate3d(-88px, 0, 0);
}
[data-dpr="3"] .c-navbar-menu {
  width: 132px;
}
[dir][data-dpr="3"] .c-navbar-menu {
  padding-top: 132px;
}
[dir=ltr][data-dpr="3"] .c-navbar-menu {
  -webkit-transform: translate3d(132px, 0, 0);
          transform: translate3d(132px, 0, 0);
}
[dir=rtl][data-dpr="3"] .c-navbar-menu {
  -webkit-transform: translate3d(-132px, 0, 0);
          transform: translate3d(-132px, 0, 0);
}
[dir] .c-navbar-menu.opened {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.c-navbar-menu li ul {
  display: none;
}
.c-navbar-menu li li {
  display: block;
}
.c-navbar-menu li li a {
  display: block;
}
.c-navbar-menu li:active ul {
  display: block;
}
[dir] .c-navbar-menu li:active ul {
  background-color: rgb(230, 230, 230);
}
.c-navbar-menu a {
  display: block;
  text-decoration: none;
}
[dir] .c-navbar-menu a {
  text-align: center;
}
[data-dpr="1"] .c-navbar-menu a {
  line-height: 44px;
}
[data-dpr="2"] .c-navbar-menu a {
  line-height: 88px;
}
[data-dpr="3"] .c-navbar-menu a {
  line-height: 132px;
}
[dir] .c-navbar-menu a:active {
  background-color: rgb(242, 242, 242);
}
.c-navbar-menu a.router-link-active {
  color: #F43531;
}
[dir] .c-navbar-menu a.router-link-active {
  background-color: rgb(254, 231, 231);
}



