
.c-reddot {
  position: relative
}
.c-reddot::after {
  content: ' ';
  position: absolute
}
[dir] .c-reddot::after {
  background-color: #F43531;
  border-radius: 50%
}
[data-dpr="1"] .c-reddot::after {
  top: -3px;
  width: 6px;
  height: 6px
}
[dir=ltr][data-dpr="1"] .c-reddot::after {
  right: -3px
}
[dir=rtl][data-dpr="1"] .c-reddot::after {
  left: -3px
}
[data-dpr="2"] .c-reddot::after {
  top: -6px;
  width: 12px;
  height: 12px
}
[dir=ltr][data-dpr="2"] .c-reddot::after {
  right: -6px
}
[dir=rtl][data-dpr="2"] .c-reddot::after {
  left: -6px
}
[data-dpr="3"] .c-reddot::after {
  top: -9px;
  width: 18px;
  height: 18px
}
[dir=ltr][data-dpr="3"] .c-reddot::after {
  right: -9px
}
[dir=rtl][data-dpr="3"] .c-reddot::after {
  left: -9px
}



