html {
  font-family: 'Helvetica Neue', Helvetica, STHeiTi, Arial, sans-serif;
  line-height: 1.5;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  height: 100%;
}

body {
  color: #333;
  height: 100%;
}

[dir] body {
  margin: 0;
  background-color: white;
}

[dir] h1, [dir] h2, [dir] h3, [dir] h4, [dir] h5, [dir] h6, [dir] p, [dir] dl, [dir] dd, [dir] form, [dir] ul, [dir] ol, [dir] pre, [dir] blockquote, [dir] textarea, [dir] input, [dir] figure, [dir] button {
  margin: 0;
}

[data-dpr="1"] h1, [data-dpr="1"] h2, [data-dpr="1"] h3, [data-dpr="1"] h4, [data-dpr="1"] h5, [data-dpr="1"] h6, [data-dpr="1"] p, [data-dpr="1"] dl, [data-dpr="1"] dd, [data-dpr="1"] form, [data-dpr="1"] ul, [data-dpr="1"] ol, [data-dpr="1"] pre, [data-dpr="1"] blockquote, [data-dpr="1"] textarea, [data-dpr="1"] input, [data-dpr="1"] figure, [data-dpr="1"] button {
  font-size: 16px;
}

[data-dpr="2"] h1, [data-dpr="2"] h2, [data-dpr="2"] h3, [data-dpr="2"] h4, [data-dpr="2"] h5, [data-dpr="2"] h6, [data-dpr="2"] p, [data-dpr="2"] dl, [data-dpr="2"] dd, [data-dpr="2"] form, [data-dpr="2"] ul, [data-dpr="2"] ol, [data-dpr="2"] pre, [data-dpr="2"] blockquote, [data-dpr="2"] textarea, [data-dpr="2"] input, [data-dpr="2"] figure, [data-dpr="2"] button {
  font-size: 32px;
}

[data-dpr="3"] h1, [data-dpr="3"] h2, [data-dpr="3"] h3, [data-dpr="3"] h4, [data-dpr="3"] h5, [data-dpr="3"] h6, [data-dpr="3"] p, [data-dpr="3"] dl, [data-dpr="3"] dd, [data-dpr="3"] form, [data-dpr="3"] ul, [data-dpr="3"] ol, [data-dpr="3"] pre, [data-dpr="3"] blockquote, [data-dpr="3"] textarea, [data-dpr="3"] input, [data-dpr="3"] figure, [data-dpr="3"] button {
  font-size: 48px;
}

[dir] ul, [dir] ol, [dir] th, [dir] td, [dir] button, [dir] textarea, [dir] input {
  padding: 0;
}

input,
textarea,
keygen,
select,
button {
  font-family: Helvetica Neue, Helvetica, STHeiTi, Arial, sans-serif;
  line-height: 1.5;
  color: #333;
}

[data-dpr="1"] input, [data-dpr="1"] textarea, [data-dpr="1"] keygen, [data-dpr="1"] select, [data-dpr="1"] button {
  font-size: 16px;
}

[data-dpr="2"] input, [data-dpr="2"] textarea, [data-dpr="2"] keygen, [data-dpr="2"] select, [data-dpr="2"] button {
  font-size: 32px;
}

[data-dpr="3"] input, [data-dpr="3"] textarea, [data-dpr="3"] keygen, [data-dpr="3"] select, [data-dpr="3"] button {
  font-size: 48px;
}

button,
input,
textarea {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

[dir] button {
  background: none;
  border: none;
  cursor: pointer;
}

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
  color: #CCC;
}

input::placeholder, textarea::placeholder {
  color: #CCC;
}

ul,
ol {
  list-style: none;
}

code,
kbd,
pre,
samp {
  font-family: Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}

pre code {
  font-size: inherit;
  color: inherit;
  white-space: pre-wrap;
}

[dir] pre code {
  padding: 0;
  background-color: transparent;
  border-radius: 0;
}

i,
em {
  font-style: normal;
}

a {
  color: #38ADFF;
  text-decoration: none
}

a:hover {
  text-decoration: none;
}

a:active {
  color: #F43531;
}

article,
footer,
header,
nav,
section {
  display: block;
}

[v-cloak] {
  display: none;
}

.router-link-active {
  color: #38ADFF;
}

.slide-enter-active,
.slide-leave-active {
  -webkit-transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}

.slide-enter,
.slide-leave-active {
  opacity: 0;
}

[dir] .slide-enter, [dir] .slide-leave-active {
  -webkit-transform: translate3d(0, -1rem, 0);
          transform: translate3d(0, -1rem, 0);
}

.slide-up-enter-active,
.slide-up-leave-active {
  -webkit-transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}

.slide-up-enter,
.slide-up-leave-active {
  opacity: 0;
}

[dir] .slide-up-enter, [dir] .slide-up-leave-active {
  -webkit-transform: translate3d(0, 1rem, 0);
          transform: translate3d(0, 1rem, 0);
}

.slide-left-enter-active,
.slide-left-leave-active {
  -webkit-transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}

.slide-left-enter,
.slide-left-leave-active {
  opacity: 0;
}

[dir=ltr] .slide-left-enter, [dir=ltr] .slide-left-leave-active {
  -webkit-transform: translate3d(1rem, 0, 0);
          transform: translate3d(1rem, 0, 0);
}

[dir=rtl] .slide-left-enter, [dir=rtl] .slide-left-leave-active {
  -webkit-transform: translate3d(-1rem, 0, 0);
          transform: translate3d(-1rem, 0, 0);
}

.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  transition: opacity 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}

.fade-enter,
.fade-leave-active {
  opacity: 0;
}

/* paddings */

[dir][data-dpr="1"] .padding {
  padding: 10px;
}

[dir][data-dpr="2"] .padding {
  padding: 20px;
}

[dir][data-dpr="3"] .padding {
