/* alignments */

[dir=ltr] .left {
  text-align: left;
}

[dir=rtl] .left {
  text-align: right;
}

.center {
  -webkit-align-self: center;
          align-self: center;
}

[dir] .center {
  text-align: center;
}

[dir=ltr] .right {
  text-align: right;
}

[dir=rtl] .right {
  text-align: left;
}

[dir=ltr] .rotate90 {
  -webkit-transform: rotate3d(0, 0, 1, 90deg);
          transform: rotate3d(0, 0, 1, 90deg);
}

[dir=rtl] .rotate90 {
  -webkit-transform: rotate3d(0, 0, 1, -90deg);
          transform: rotate3d(0, 0, 1, -90deg);
}

[dir=ltr] .rotate90n {
  -webkit-transform: rotate3d(0, 0, 1, -90deg);
          transform: rotate3d(0, 0, 1, -90deg);
}

[dir=rtl] .rotate90n {
  -webkit-transform: rotate3d(0, 0, 1, 90deg);
          transform: rotate3d(0, 0, 1, 90deg);
}

